@-moz-document url-prefix(){.leftbar{background-color:rgba(0,0,0,0.1)}}.leftbar{flex:0 0 220px;width:220px;height:100%;backdrop-filter:drop-shadow(-16px 16px 20px) invert(10%) blur(10px);box-shadow:0 1px 2px rgba(0,0,0,0.1);z-index:200;transition:.2s}@media all and (max-width:760px){.leftbar{position:fixed;left:-220px;bottom:0;height:calc(100% - 48px);opacity:0;visibility:hidden;background-color:rgba(0,0,0,0.1)}}@media all and (max-width:760px){.leftbar.leftbarDisplay{opacity:1;visibility:visible;left:0}}.leftbar > :nth-child(1){margin:14px 14px;background-color:#fafafa;border-radius:6px;overflow:scroll;scrollbar-width:none}.leftbar > :nth-child(1) > div{position:relative;width:100%}.leftbar > :nth-child(1) > div div{padding:12px 5px;font-size:16px;text-indent:15px}.leftbar > :nth-child(1) > div:first-child{margin-top:0}.leftbar > :nth-child(1) > div:last-child{margin-bottom:0}.leftbar > :nth-child(1) > div:hover{background-color:#ed8e33}.leftbar > :nth-child(1) > div.select{background-color:#ed8e33}.leftbarStateBtn{display:none;position:fixed;left:20px;bottom:77px;z-index:201;position:absolute;width:42px;height:42px;background-color:#ed8e33;cursor:pointer;border-radius:30px;border:2px solid rgba(0,0,0,0);user-select:none}.leftbarStateBtn > svg{width:24px;height:24px;position:absolute;left:9px;top:9px}.leftbarStateBtn > svg > :nth-child(2){fill:#fafafa}.leftbarStateBtn:hover,.leftbarStateBtn:focus{background-color:#fafafa;border-color:#cc6f15}.leftbarStateBtn:hover > svg > :nth-child(2),.leftbarStateBtn:focus > svg > :nth-child(2){fill:#cc6f15}@media all and (max-width:760px){.leftbarStateBtn{display:block}}